@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
	list-style: none;
}

:root {
	
	--color-orange: #ff9900;
	--color-white: #fff;
	--color-grey-lt: #f1f1eb;
  --color-grey-md: #e5e5df;
  --header-padding: 2em;
}

body {
	color: #333;
	background-color: #e5e5df;
	background-color: var(--color-grey-md);
	font-family: 'Ubuntu', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-y: hidden;
}

/* =================================
    shared classes across components
   =================================       
 */

.container {
	width: 95%;
	max-width: 1100px;
	margin: 0 auto;
}

.flex {
	display: flex;
}

.joke {
    background: var(--color-grey-lt);
    padding: 2em;
    margin: 1em 0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .23);
    transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1) ;
    min-width: 200px;
}

.joke-container {
    padding: 3em;
    text-align: center;
    flex-direction: column;
    align-items: center;
}

.jokes__loader {
	width: 50vw;
	text-align: center;
}

.sub-title {
    padding: 5em 0;
    display: grid;
    place-items: center;
    color: var(--color-orange);
}

.joke:hover {
    box-shadow: 0 0 2px rgba(0, 0, 0, .23);
    transform: perspective(100px) translateZ(3px);
}

.joke__image__container {
    width: 100%;
}

.joke__image {
    width: 50%;
}

.joke__name, .joke__url {
    margin: 1em 0;
}

.joke__name {
    color: rgb(0,128,128);
}

.joke__url {
    display: inline-block;
    color: var(--color-white);
    font-weight: bold;
    padding: .5em 1em;
    background: var(--color-orange);
}

.btn {
    padding: 1em 2em;
    color: #333;
    background: transparent;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-weight: bold;
    border-radius: 3px;
    box-shadow: 0 0 4px 0 rgba(255, 153, 0, 0.7);
    transition: background 300ms linear;
    margin: 1em;
}

.btn:hover,
.btn:focus {
    color: var(--color-white);
    background: var(--color-orange);
}


@media only screen and (max-width: 600px) {
    .joke-container {
        padding: 1em;
    }
    .jokes__loader {
        display: grid;
        place-items: center;
        width: 80vw;
        text-align: unset;
    }
    .sub-title {
        padding: 3em 0;
        font-size: 0.9rem;
    }
    .joke {
        padding: 1em;
    }
}

@media only screen and (min-width: 600px) {
    .joke {
        max-width: 300px;
        width: 70%;
        padding: 1em;
        margin: 0 0 .5em 0;
    }
}


.category__item {
    text-transform: capitalize;
    font-weight: bold;
    margin: .3em;
    padding: .2em;
    font-size: 0.9rem;
    transition: background 300ms linear;
}
.category__item a {
    color: rgba(0,0,0,0.5)
}

.category__item:hover,
.category__item:focus {
    background: var(--color-grey-lt);
}
.title {
    border-bottom: 1px solid var(--color-grey-md);
    margin-bottom: 1em;
    padding: 1em 0 .5em 0;
}
.main {
	background-color: var(--color-white);
    height: calc(100vh - calc(var(--header-padding)*2));
    display: grid;
    grid: auto / 200px 1fr;
}

.main {
	height: 100vh;
}

.main__title {
    text-align: center;
}
.sidebar {
    padding: 1em;
    border-right: 2px solid var(--color-grey-md);
    height: 100vh;

}

.main-home {
    height: 100vh;
}

.main-home h2{
    margin: 1em 0;
    
}

@media only screen and (max-width:600px) {
	.main {
        display: flex;
        align-items: flex-start;
	}
    .main__title {
        font-size: 1.3rem;
    }
}

/* ================================
    header styles
   =================================       
 */

.header {
	color: var(--color-white);
	background-color: var(--color-orange);
    padding: var(--header-padding);
}

.header > div {
    justify-content: space-between;
}

.header__logo {
	color: inherit;
}

.header__logo sup {
	font-size: 0.6rem;
	font-weight: bold;
}

.nav__list__item {
	margin-left: 1em;
}

.nav__link {
	color: inherit;
	text-transform: uppercase;
	font-weight: bold;
}

@media only screen and (max-width:600px) {
	.header > div {
		flex-direction: column;
		align-items: center;
	}
	.header__logo {
		margin-bottom: 1em;
	}
	.nav__link {
		font-size: 0.9rem;
	}
}

